import { Slide } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import React, { useState } from "react";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const DialogNewBoard = ({ isOpen, onClickClose, onClickCreate }) => {
  const [title, setTitle] = useState("");

  const handleChangeTitle = (e) => {
    const { value } = e.target;
    setTitle(value);
  };

  const handleClickCreate = () => {
    if (!title) {
      alert("Please fill board's name!");
      return;
    }

    onClickCreate(title);
    setTitle("");
    onClickClose();
  };

  return (
    <div>
      <Dialog
        open={isOpen}
        TransitionComponent={Transition}
        keepMounted
        aria-labelledby="form-dialog-title"
      >
        <div style={{ width: "400px" }}>
          <DialogTitle id="form-dialog-title">New Board</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Enter name..."
              type="text"
              fullWidth
              onChange={handleChangeTitle}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={() => onClickClose()} color="primary">
              Cancel
            </Button>
            <Button onClick={() => handleClickCreate()} color="primary">
              Create
            </Button>
          </DialogActions>
        </div>
      </Dialog>
    </div>
  );
};

export default DialogNewBoard;
